.watch {
  box-sizing: border-box;
  background-color: bisque;
  border: 5px solid #301f01;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
  box-shadow: 0 0 4vmin #00000080;
}

.watch__center {
  z-index: 1;
  background-color: #301f01;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
}

.watch__minute-hand {
  transform-origin: bottom;
  background-color: #805012;
  border-radius: 50%;
  width: 8px;
  height: 25%;
  animation: 60s linear 1s infinite spin;
  position: absolute;
  top: 20vmin;
}

.watch__second-hand {
  transform-origin: bottom;
  background-color: #ce5151;
  border-radius: 25%;
  width: 4px;
  height: 50%;
  animation: 5s linear 1s infinite spin;
  position: absolute;
  top: 0;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.page {
  background-color: #e6be8e;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

